import React, { Component } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
var ReactDOM = require('react-dom');
var ReactBsTable  = require('react-bootstrap-table');
var BootstrapTable = ReactBsTable.BootstrapTable;
var TableHeaderColumn = ReactBsTable.TableHeaderColumn;

class Bids extends Component {
  constructor(props) {
    super(props);

    this.state = {
      api_res:null
    };

  }

  componentDidMount() {
    fetch(`https://dddhibs.herokuapp.com/user/comments/${localStorage.postedby}`)
    .then(res=>res.json())
    .then(res=> this.setState({
      api_res:res
    }))

  }

  componentWillUpdate() {
    fetch(`https://dddhibs.herokuapp.com/user/comments/${localStorage.postedby}`)
    .then(res=>res.json())
    .then(res=> this.setState({
      api_res:res
    }))

  }

  render() {

    let bids_holder =[]
    let sorted_bid = []
    let sorted_display = []



    return (
      <>
      {
        (this.state.api_res!= null) ? this.state.api_res.map(x=>{

          x.bids.map(q=>{
            (q.itemname == localStorage.view_item) ? bids_holder.push(q):console.log('')
          })
        }):console.log('loading')
      }


      {
      bids_holder.map(x=>{
        x.amount = parseInt(x.amount)
        sorted_bid.push(x)
      })


      }





      <div style={{width: '100%',height: '100%','align-items':'center'}}>
      <BootstrapTable style={{'text-align': 'center'}} data={sorted_bid.sort(function(a,b){
        return b.amount - a.amount
      })} width='100%' >
      <TableHeaderColumn style={{'text-align': 'center'}} isKey dataField='bedby'>Bid By</TableHeaderColumn>
      <TableHeaderColumn dataField='amount'>Amount </TableHeaderColumn>
    </BootstrapTable>
  </div>









</>

    );
  }

}

export default Bids;
